                </p>
            </el-col>

            <el-col :xs="12" :sm="8" :md="6" :lg="4" :xl="2">
                <p class="btnList">
                    <el-button type="primary" @click="exportExcel" size="small">
                        <i class="el-icon-download" />
                        
                    </el-button>
                </p>
            </el-col>

            <el-col :xs="12" :sm="8" :md="6" :lg="4" :xl="2">
                <p class="btnList">
                    <el-button type="primary" @click="exportExcel" size="small">
                        <i class="el-icon-download" />
                        
                    </el-button>
                </p>
            </el-col>
        </el-row>

        <el-row :gutter="20">
            <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <div class="grid-content bg-purple">
                    <el-card class="box-card">
                        <el-tree
                            :data="treeData"
                            :props="defaultProps"
                            @node-click="handleNodeClick"
                            :default-expand-all="true"
                            :expand-on-click-node="false"
                        ></el-tree>
                    </el-card>
                </div>
            </el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <div class="grid-content bg-purple-light">
                    <el-card class="box-card">
                        <el-descriptions title="" :column="2" border>
                            <el-descriptions-item
                                label=""
                                label-class-name="my-label"
                                content-class-name="my-content"
                                :content-style="{ 'text-align': 'center' }"
                            >
                                {{ personalData?.peopleName }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.iphone }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.address }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }">
                                <img :src="personalData?.peoplePic" alt="" style="width: 100px" />
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.branch }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.houseBranch }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.remarks }}
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >{{ personalData?.peopleDescription }}
                            </el-descriptions-item>
                        </el-descriptions>
                    </el-card>
                    <el-card class="box-card" style="margin-top: 10px">
                        <el-descriptions title="" :column="3" border>
                            <el-descriptions-item
                                label=""
                                label-class-name="my-label"
                                content-class-name="my-content"
                                :content-style="{ 'text-align': 'center' }"
                            >
                                kooriookami
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >18100000000
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                >
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }">
                                <el-tag size="small"></el-tag>
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                            <el-descriptions-item label="" :content-style="{ 'text-align': 'center' }"
                                > 1188 
                            </el-descriptions-item>
                        </el-descriptions>
                    </el-card>
                </div>
            </el-col>
        </el-row>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="50%"
                modal
                :close-on-click-modal="true"
                :append-to-body="true"
                :modal-append-to-body="false"
            >
                <el-form ref="oneForm" label-position="center" label-width="150px" :model="dataForm" :rules="rules">
                    <el-form-item label=": " prop="area"></el-form-item>

                    <el-form-item label=": " prop="branchName">
                        <el-input
                            v-model.lazy.trim="dataForm.branchName"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="branchNumber">
                        <el-input
                            v-model.lazy.trim="dataForm.branchNumber"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="lastName">
                        <el-select v-model.lazy.trim="dataForm.lastName" placeholder="">
                            <el-option
                                v-for="item in options2"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            >
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label=": " prop="branchZhi">
                        <el-input
                            v-model.lazy.trim="dataForm.branchZhi"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="enableNumber">
                        <el-input
                            v-model="dataForm.enableNumber"
                            clearable
                            placeholder=""
                            style="width: 60%"
                            type="number"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="enableNumber">
                        <el-switch v-model="dataForm.branStatue" active-text="" inactive-text=""></el-switch>
                    </el-form-item>
                </el-form>
                <div slot="footer" style="text-align: center" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button
                        type="primary"
                        @click="updateFlag === false ? handleAdd(dataForm) : handleUpdate(dataForm)"
                        > 
                    </el-button>
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { customizeMsgBox, msgBox } from "@/util/msgbox"
import { requestByDelete, requestByGet, requestByPost, requestByPut } from "@/api/globalApi"
import api, { peopleList } from "@/api/apis"
import { pcaTextArr } from "element-china-area-data"
import util from "@/util/index"
import { BranchOption } from "@/api/project"

export default {
    name: "OrderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        var validatePass = (rule, value, callback) => {
            console.log(value)
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        return {
            dataLoading: false, // 
            personalData: null,
            idKey: "peopleName",
            multipleSelection: [], // 
            parentKey: "peopleParent",
            treeData: [],
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10, branchName: "" }, //  1,10 ----  +
            pcaTextArr,
            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 
            dataForm: {
                area: [],
                branchName: "",
                branchNumber: "",
                lastName: "",
                branchZhi: "",
                enableNumber: 0,
                branStatue: false
            },
            like: true,
            value1: 4154.564,
            value2: 1314,
            title: "",
            rules: {
                area: [{ validator: validatePass, trigger: "blur" }]
            },
            fileList: [],
            options: BranchOption,

            defaultProps: {
                children: "children",
                label: "label"
            },

            options2: [
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                }
            ]
        }
    },
    computed: {},
    created() {
        this.fetchData()
    },
    methods: {
        peopleList() {
            return peopleList
        },
        // 
        fetchData() {
            this.dataLoading = true
            requestByGet(api.peopleList, this.params)
                .then(value => {
                    this.dataLoading = false
                    this.treeData = util.buildTree(value.rspdata, this.idKey, this.parentKey)
                })
                .catch(error => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPost(api.Product, JSON.stringify(formdata)).then(value => {
                        if (value.code === 200) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        } else {
                            customizeMsgBox(4, "")
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            let reqData = {
                branId: formdata.branId,
                branArea: formdata.area.join(","),
                branName: formdata.branchName,
                branNumber: formdata.branchNumber,
                branStatue: formdata.branStatue === true ? 1 : 0,
                branZhiBei: formdata.branchZhi,
                enableNumber: formdata.enableNumber,
                lastName: formdata.lastName
            }

            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPut(api.branch, JSON.stringify(reqData)).then(value => {
                        console.log(value)
                        if (value.code === 200 || value.code === 300) {
                            if (value.code === 300) customizeMsgBox(3, "")

                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: ""
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then(value => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        handleDelete(index, row) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    console.log(index, row)

                    let idArr = { ids: [] }

                    idArr.ids.push(row.branId)

                    requestByDelete(api.branch, JSON.stringify(idArr)).then(value => {
                        if (value.code === 200) {
                            customizeMsgBox(1, "")
                            this.fetchData()
                        }
                    })
                })
                .catch(() => {
                    customizeMsgBox(4, "")
                })
        },
        // 
        addClick(title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = false
            // 
            this.dataForm = {}
        },
        // 
        updateClick(val, title) {
            let data = {
                branId: val.branId,
                area: val.branArea.split(","),
                branchName: val.branName,
                branchNumber: val.branNumber,
                lastName: val.lastName,
                branchZhi: val.branZhiBei,
                enableNumber: val.enableNumber,
                branStatue: val.branStatue !== 0
            }

            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = data

            this.$forceUpdate
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },
        timestamp() {
            var time = new Date()
            var y = time.getFullYear()
            var m = time.getMonth() + 1
            var d = time.getDate()
            var h = time.getHours()
            var mm = time.getMinutes()
            var s = time.getSeconds()
            return "" + y + this.add0(m) + this.add0(d) + this.add0(h) + this.add0(mm) + this.add0(s)
        },
        add0(m) {
            return m < 10 ? "0" + m : m
        },
        searchList() {
            this.fetchData()
        },
        clearUpdata() {
            document.getElementById("fileName").value = ""
            this.dataForm.productUrl = undefined
            this.$forceUpdate()

            console.log(document.getElementById("fileName").files[0])
        },
        handleNodeClick(event) {
            this.personalData = event

            console.log(this.personalData)
        }
    }
}
</script>

