<template>
    <div class="app-container">
        <!-- <el-button @click="addClick('')">
      <i class="el-icon-plus" />
      
    </el-button>
    <el-button @click="handleMultiDelete">
      <i class="el-icon-delete" />
      
    </el-button> -->
        <el-table
            v-loading="dataLoading"
            style="margin-top: 10px"
            :data="dataList"
            element-loading-text="Loading"
            border
            @selection-change="handleSelectionChange"
        >
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.orderNo }}</span>
                </template>
            </el-table-column>
            <el-table-column label="id" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.payWay }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.totalFee }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span
                        :style="
                            scope.row.orderStatus == ''
                                ? 'color:red'
                                : scope.row.orderStatus == ''
                                ? 'color:green'
                                : 'color:#efc467'
                        "
                        >{{ scope.row.orderStatus }}</span
                    >
                </template>
            </el-table-column>
            <el-table-column fixed="right" label="" align="center" width="100">
                <template slot-scope="scope">
                    <el-button
                        type="warning"
                        size="mini"
                        icon="el-icon-edit"
                        circle
                        @click="updateClick(scope.row, '')"
                    />
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 30px; float: right">
            <el-pagination
                background
                :current-page="params.pageNum"
                :page-sizes="pageSizes"
                :page-size="params.pageSize"
                layout="total, prev, pager, next, sizes"
                :total="totalSize"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="70%"
                modal
                :close-on-click-modal="false"
                :append-to-body="true"
            >
                <el-form ref="oneForm" label-position="right" label-width="150px" :model="dataForm" :rules="rules">
                    <el-form-item label=": " prop="productName" required>
                        <el-input
                            v-model="dataForm.productName"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="productNo" required>
                        <el-input
                            v-model="dataForm.productNo"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                </el-form>
                <div slot="footer" style="text-align: center" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button
                        type="primary"
                        @click="updateFlag == false ? handleAdd(dataForm) : handleUpdate(dataForm)"
                        > </el-button
                    >
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { msgBox, customizeMsgBox } from "@/util/msgbox"
//   import { UTCToformat } from "@/utils";
import { requestByGet, requestByPost, requestByPut, requestByDelete } from "@/api/globalApi"
import api from "@/api/apis"

export default {
    name: "orderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        return {
            dataLoading: false, // 
            dataList: [], // 
            multipleSelection: [], // 
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10 }, //  1,10 ----  +

            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 
            dataForm: {}, // dialog 
            rules: {}, // 
        }
    },
    created() {
        this.fetchData()
    },
    methods: {
        // 
        fetchData() {
            this.dataLoading = true
            requestByGet(api.orderList, this.params)
                .then((value) => {
                    console.log(value)

                    this.dataLoading = false
                    if (value.rspdata !== "" && value.rspdata !== null) {
                        this.dataList = value.rspdata.records
                        this.totalSize = value.rspdata.total
                    }
                    if (value.code == 403) {
                        localStorage.removeItem("jwt")
                    }
                })
                .catch((error) => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate((valid) => {
                if (valid) {
                    formdata.orderId = this.superiorId
                    requestByPost(api.orderProduct, JSON.stringify(formdata)).then((v) => {
                        if (v.code === 2000) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            this.$refs.oneForm.validate((valid) => {
                if (valid) {
                    requestByPut(api.orderProduct, JSON.stringify(formdata)).then((value) => {
                        if (value.code === 2000) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: "",
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then((value) => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        // 
        addClick(title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = false
            // 
            this.dataForm = {}
        },
        // 
        updateClick(val, title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = val
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },
    },
}
</script>

<style>
.inputStyle {
    width: 200px;
}

.app-container {
    width: 96%;
    margin: auto;
}
</style>
