<template>
    <div class="app-container">
        <!-- <el-button @click="addClick('')">
        <i class="el-icon-plus" />
        
      </el-button> -->
        <!-- <el-button @click="handleMultiDelete">
          <i class="el-icon-delete" />
          
        </el-button> -->
        <el-tabs v-model="params.activeName" @tab-click="handleClick">
            <el-tab-pane label="" name="0"></el-tab-pane>
            <el-tab-pane label="" name="1"></el-tab-pane>
            <el-tab-pane label="" name="2"></el-tab-pane>
            <el-tab-pane label="" name="3"></el-tab-pane>
        </el-tabs>
        <el-table
            v-loading="dataLoading"
            style="margin-top: 10px"
            :data="dataList"
            element-loading-text="Loading"
            border
            @selection-change="handleSelectionChange"
        >
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.auditShopName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.auditSubmitManager }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{
                        scope.row.auditProcess == 0 ? "" : scope.row.auditProcess == 1 ? "" : ""
                    }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.auditPeople }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="100">
                <template slot-scope="scope">
                    <el-button
                        type="success"
                        size="mini"
                        icon="el-icon-edit"
                        @click="addClick('', scope.$index, scope.row)"
                    />
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 30px; float: right">
            <el-pagination
                background
                :current-page="params.pageNum"
                :page-sizes="pageSizes"
                :page-size="params.pageSize"
                layout="total, prev, pager, next, sizes"
                :total="totalSize"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="50%"
                modal
                :close-on-click-modal="true"
                :append-to-body="true"
                :modal-append-to-body="false"
            >
                <ul class="audit">
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorName }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorAddress }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorClass }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorIphone }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorTower }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>{{ floorAduit.floorManager }}</p>
                    </li>
                    <li>
                        <p></p>
                        <p>
                            <img :src="floorAduit.floorPic" alt="" />
                        </p>
                    </li>
                    <li>
                        <p></p>
                        <p>
                            <el-input
                                type="textarea"
                                :rows="5"
                                placeholder=""
                                v-model="failMessage">
                            </el-input>
                        </p>
                    </li>
                </ul>
                <div class="options">
                    <el-button type="success" @click="applyAudit(true)"></el-button>
                    <el-button type="danger" @click="applyAudit(false)"></el-button>
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { msgBox, customizeMsgBox } from "@/util/msgbox"
//   import { UTCToformat } from "@/utils";
import { requestByGet, requestByPost, requestByPut, requestByDelete } from "@/api/globalApi"
import api from "@/api/apis"

export default {
    name: "orderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        var validatePass = (rule, value, callback) => {
            if (value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass2 = (rule, value, callback) => {
            if (value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass3 = (rule, value, callback) => {
            if (value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass4 = (rule, value, callback) => {
            if (value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        return {
            dataLoading: false, // 
            dataList: [], // 
            multipleSelection: [], // 
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10, activeName: "0" }, //  1,10 ----  +

            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 

            dataForm: {
                manageIphone: "",
                managePassword: "",
                manageRole: "",
                manageName: "",
            }, // dialog 
            rules: {
                manageIphone: [{ validator: validatePass, trigger: "blur" }],
                managePassword: [{ validator: validatePass2, trigger: "blur" }],
                manageRole: [{ validator: validatePass3, trigger: "blur" }],
                manageName: [{ validator: validatePass4, trigger: "blur" }],
            },
            fileList: [],
            roleListData: [],
            floorAduit: {},
            floorData: {},
            failMessage:""
        }
    },
    created() {
        // console.log(this.superiorId)
        this.fetchData()
    },
    methods: {
        // 
        fetchData() {
            this.dataLoading = true

            let auditListReqData = {
                pageNum: this.params.pageNum,
                pageSize: this.params.pageSize,
                auditClass: parseInt(this.params.activeName),
            }
            requestByGet(api.auditList, auditListReqData)
                .then((value) => {
                    this.dataLoading = false

                    if (value.rspdata !== "" && value.rspdata !== null) {
                        this.dataList = value.rspdata.records
                        this.totalSize = value.rspdata.total
                    } else {
                        this.dataList = []
                        this.totalSize = 1
                    }
                    if (value.code == 403) {
                        localStorage.removeItem("jwt")
                    }
                })
                .catch((error) => {
                    msgBox(1000)
                    this.dataLoading = true
                })

            let reqdata = {
                isAll: true,
            }
            requestByGet(api.roleList, reqdata)
                .then((value) => {
                    console.log(value)
                    if (value.rspdata !== "") {
                        this.roleListData = value.rspdata
                    }
                })
                .catch((error) => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate((valid) => {
                if (valid) {
                    let findRolePermission = this.roleListData.find((item) => {
                        return item.roleName == formdata.manageRole
                    })

                    formdata.managePermission = findRolePermission.rolePermission

                    requestByPost(api.manager, JSON.stringify(formdata)).then((value) => {
                        console.log(value)
                        if (value.code === 200) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        } else {
                            customizeMsgBox(4, value.msg)
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            this.$refs.oneForm.validate((valid) => {
                if (valid) {
                    requestByPut(api.orderProduct, JSON.stringify(formdata)).then((value) => {
                        if (value.code === 2000) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: "",
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then((value) => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        handleDelete(index, row) {
            console.log(row.manageId)

            if (row.manageId == "0") {
                customizeMsgBox(4, "")
            } else {
                this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                })
                    .then(() => {
                        console.log(index, row)

                        let idArr = { ids: [] }

                        idArr.ids.push(row.manageId)

                        requestByDelete(api.manager, JSON.stringify(idArr)).then((value) => {
                            if (value.code === 200) {
                                customizeMsgBox(1, "")
                                this.fetchData()
                            }
                        })
                    })
                    .catch(() => {
                        customizeMsgBox(4, "")
                    })
            }
        },

        handleClick(tab, event) {
            this.fetchData()
        },
        // 
        addClick(title, index, row) {
            this.dialogTitle = title

            requestByGet(api.floor, { floorId: row.auditFloorId })
                .then((value) => {
                    this.floorData = row

                    this.floorAduit = value.rspdata

                    this.dialogFormVisible = true
                })
                .catch((error) => {
                    msgBox(1000)
                })
        },

        applyAudit(sucessOrFail) {
            this.floorData.auditPeople = localStorage.getItem("userName")

           if(sucessOrFail){
                requestByPost(api.auditProcess, this.floorData, { isok: true }).then((value) => {
                    console.log(value)
                    if (value.code === 200) {
                        this.dialogFormVisible = false
                        this.fetchData()
                        // 
                        this.floorAduit = {}
                        this.floorData = {}
                    } else {
                        customizeMsgBox(4, value.msg)
                    }
                })
           }else{

                if(this.failMessage != ""){

                    this.floorData.auditFailMessage = this.failMessage

                    requestByPost(api.auditProcess, this.floorData, { isok: false }).then((value) => {
                        if (value.code === 200) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.floorAduit = {}
                            this.floorData = {}
                        } else {
                            customizeMsgBox(4, value.msg)
                        }
                    })
                }else{
                    customizeMsgBox(4,"")
                }
                
           }
        },
        // 
        updateClick(val, title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = val
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },

        handleRemove(file) {
            console.log(file)

            this.dataForm.productUrl = undefined
        },
        handleSucess(response) {
            if (response.code == 500) {
                this.$refs.upload.clearFiles()
            } else if (response.code == 200) {
                this.dataForm.productUrl = response.rspdata.returnFileUrl
            } else {
                customizeMsgBox(4, "")
            }
        },
        handleBeforeUpload() {},
        handleError() {
            this.dataForm.productUrl = undefined
            this.$refs.upload.clearFiles()
        },
        handleChange(file) {
            const loading = this.$loading({
                lock: true,
                text: "",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
            })

            if (file.response != undefined && file.status == "success") {
                loading.close()

                switch (file.response.code) {
                    case 200:
                        customizeMsgBox(1, "")
                        break
                    case 500:
                        customizeMsgBox(4, "")
                        break
                    default:
                        customizeMsgBox(4, "")
                        break
                }
            } else if (file.response == undefined && file.status == "fail") {
                loading.close()
                customizeMsgBox(4, "")
            }

            console.log(file, file.response)
        },
    },
}
</script>

<style lang="less">
.inputStyle {
    width: 200px;
}

.app-container {
    width: 96%;
    margin: auto;
}

.options {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.audit {
    li {
        display: flex;
        justify-content: center;
        align-items: center;

        p {
            font-size: 18px;
            width: 50%;

            img {
                width: 100%;
            }
        }
    }
}
</style>
