<template>
    <div class="app-container">
        <div class="top_search">
            <el-select v-model="params.branchName" placeholder="">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                ></el-option>
            </el-select>
            <el-input placeholder="" v-model="params.content" clearable class="search_input"></el-input>
            <el-button @click="searchList">
                <i class="el-icon-search" />
                
            </el-button>
            <el-button type="primary" @click="addClick('')">
                <i class="el-icon-plus" />
                
            </el-button>
            <el-button type="primary" @click="exportExcel">
                <i class="el-icon-download" />
                Excel
            </el-button>
        </div>
        <el-table
            v-loading="dataLoading"
            style="margin-top: 10px"
            :data="dataList"
            element-loading-text="Loading"
            border
            @selection-change="handleSelectionChange"
        >
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.area }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.houseBranch }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.houseBranchName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row.houseNumber }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row.totalBranch }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.totalDescription }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row.secondBranchNumber }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.secondBranchDescription }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.houseDescription }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.establishPeople }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.submitCode }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.updataTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="150">
                <template slot-scope="scope">
                    <el-button type="text" @click="handleDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" @click="updateClick(scope.row, '')"></el-button>
                    <el-button type="text"></el-button>
                    <el-button type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 30px; float: right">
            <el-pagination
                background
                :current-page="params.pageNum"
                :page-sizes="pageSizes"
                :page-size="params.pageSize"
                layout="total, prev, pager, next, sizes"
                :total="totalSize"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="50%"
                modal
                :close-on-click-modal="true"
                :append-to-body="true"
                :modal-append-to-body="false"
            >
                <el-form ref="oneForm" label-position="right" label-width="150px" :model="dataForm" :rules="rules">
                    <div style="display: flex;justify-content: space-around">
                        <p style="width: 45%">
                            <el-form-item label=": " prop="area">
                                <el-cascader v-model="dataForm.area" size="large" :options="pcaTextArr"></el-cascader>
                            </el-form-item>

                            <el-form-item label=": " prop="houseBranchName">
                                <el-input
                                    v-model.lazy.trim="dataForm.houseBranchName"
                                    clearable
                                    placeholder=""
                                />
                            </el-form-item>
                            <el-form-item label=": ">
                                <el-input value="0" clearable disabled type="number" />
                            </el-form-item>

                            <el-form-item label=": " prop="houseBranch">
                                <el-select
                                    v-model.lazy.trim="dataForm.houseBranch"
                                    placeholder=""
                                    style="width: 100%"
                                >
                                    <el-option
                                        v-for="item in options"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label=": " prop="totalDescription">
                                <el-input v-model="dataForm.totalDescription" clearable placeholder="" />
                            </el-form-item>
                            <el-form-item label=": " prop="totalBranch">
                                <el-input
                                    v-model="dataForm.totalBranch"
                                    clearable
                                    placeholder=""
                                    type="number"
                                />
                            </el-form-item>
                            <el-form-item label=": " prop="secondBranchDescription">
                                <el-input v-model="dataForm.secondBranchDescription" clearable placeholder="" />
                            </el-form-item>
                        </p>
                        <p style="width: 45%;">
                            <el-form-item label=": " prop="secondBranchNumber">
                                <el-input
                                    v-model="dataForm.secondBranchNumber"
                                    clearable
                                    placeholder=""
                                    type="number"
                                />
                            </el-form-item>
                            <el-form-item label=": " prop="houseDescription">
                                <el-input v-model="dataForm.houseDescription" clearable placeholder="" />
                            </el-form-item>
                            <el-form-item label=": " prop="houseBranchNumber">
                                <el-input
                                    v-model="dataForm.houseBranchNumber"
                                    clearable
                                    placeholder=""
                                    type="number"
                                />
                            </el-form-item>
                            <el-form-item label=": " prop="houseSort">
                                <el-input v-model="dataForm.houseSort" clearable placeholder="" type="number" />
                            </el-form-item>
                            <el-form-item label=": " prop="roomNumber">
                                <el-input
                                    v-model="dataForm.roomNumber"
                                    clearable
                                    placeholder=""
                                    type="number"
                                />
                            </el-form-item>
                            <!--                            <el-form-item label=": " prop="submitCode">-->
                            <!--                                <el-input-->
                            <!--                                    v-model="dataForm.submitCode"-->
                            <!--                                    clearable-->
                            <!--                                    type="number"-->
                            <!--                                    placeholder=""-->
                            <!--                                />-->
                            <!--                            </el-form-item>-->
                            <!--                            <el-form-item label=": " prop="resource">-->
                            <!--                                <el-radio-group v-model="resource" size="medium">-->
                            <!--                                    <el-radio border label=""></el-radio>-->
                            <!--                                    <el-radio border label=""></el-radio>-->
                            <!--                                </el-radio-group>-->
                            <!--                            </el-form-item>-->
                        </p>
                    </div>
                </el-form>
                <div slot="footer" style="text-align: center" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button
                        type="primary"
                        @click="updateFlag === false ? handleAdd(dataForm) : handleUpdate(dataForm)"
                        > 
                    </el-button>
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { msgBox, customizeMsgBox } from "@/util/msgbox"
import { requestByGet, requestByPost, requestByPut, requestByDelete } from "@/api/globalApi"
import api from "@/api/apis"
import { pcaTextArr } from "element-china-area-data"
import util from "@/util/index"

export default {
    name: "OrderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        var validatePass = (rule, value, callback) => {
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        return {
            dataLoading: false, // 
            dataList: [], // 
            multipleSelection: [], // 
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10, branchName: "", content: "" }, //  1,10 ----  +
            pcaTextArr,
            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 
            dataForm: {
                area: [],
                houseBranchName: "",
                houseBranch: "",
                totalBranch: "",
                totalDescription: "",
                secondBranchNumber: "",
                secondBranchDescription: "",
                houseBranchNumber: "",
                houseSort: "",
                roomNumber: "",
                houseDescription: ""
            },

            rules: {
                area: [{ validator: validatePass, trigger: "blur" }],
                houseBranchName: [{ validator: validatePass, trigger: "blur" }],
                houseBranch: [{ validator: validatePass, trigger: "blur" }],
                totalBranch: [{ validator: validatePass, trigger: "blur" }],
                totalDescription: [{ validator: validatePass, trigger: "blur" }],
                secondBranchNumber: [{ validator: validatePass, trigger: "blur" }],
                secondBranchDescription: [{ validator: validatePass, trigger: "blur" }],
                houseBranchNumber: [{ validator: validatePass, trigger: "blur" }],
                houseSort: [{ validator: validatePass, trigger: "blur" }],
                roomNumber: [{ validator: validatePass, trigger: "blur" }],
                houseDescription: [{ validator: validatePass, trigger: "blur" }]
            },
            fileList: [],
            options: [],
            resource: ""
        }
    },
    created() {
        // console.log(this.superiorId)
        this.fetchData()
    },
    methods: {
        // 
        fetchData() {
            this.dataLoading = true

            requestByGet(api.allBranch)
                .then(value => {
                    this.options = value.rspdata.map(item => {
                        return {
                            value: item,
                            label: item
                        }
                    })

                    this.options.push({
                        value: "",
                        label: ""
                    })
                })
                .catch(error => {})

            requestByGet(api.houseList, this.params)
                .then(value => {
                    this.dataLoading = false

                    if (value.rspdata !== "" && value.rspdata !== null) {
                        this.dataList = value.rspdata.records
                        this.totalSize = value.rspdata.total
                    } else {
                        this.dataList = []
                        this.totalSize = 0
                    }
                })
                .catch(error => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    formdata.area = formdata.area.join(",")
                    requestByPost(api.house, JSON.stringify(formdata)).then(value => {
                        if (value.code === 200) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                            customizeMsgBox(1, "")
                        } else {
                            customizeMsgBox(4, "")
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            console.log()
            if (Array.isArray(formdata.area)) formdata.area = formdata.area.join(",")

            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPut(api.house, JSON.stringify(formdata)).then(value => {
                        if (value.code === 200 || value.code === 300) {
                            if (value.code === 300) customizeMsgBox(3, "")

                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}

                            customizeMsgBox(1, "")
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: ""
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then(value => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        handleDelete(index, row) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    let idArr = { ids: [] }

                    idArr.ids.push(row.houseBranchID)

                    requestByDelete(api.house, JSON.stringify(idArr)).then(value => {
                        if (value.code === 200) {
                            customizeMsgBox(1, "")
                            this.fetchData()
                        }
                    })
                })
                .catch(() => {
                    customizeMsgBox(4, "")
                })
        },
        // 
        addClick(title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = false
            // 
            this.dataForm = { resource: "" }
        },
        // 
        updateClick(val, title) {
            let data = {
                houseBranchID: val.houseBranchID,
                area: val.area.split(","),
                houseBranchName: val.houseBranchName,
                houseBranch: val.houseBranch,
                totalBranch: val.totalBranch,
                totalDescription: val.totalDescription,
                secondBranchNumber: val.secondBranchNumber,
                secondBranchDescription: val.secondBranchDescription,
                houseBranchNumber: val.houseBranchNumber,
                houseSort: val.houseSort,
                roomNumber: val.roomNumber,
                houseDescription: val.houseDescription
            }

            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = data

            this.$forceUpdate
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },
        timestamp() {
            var time = new Date()
            var y = time.getFullYear()
            var m = time.getMonth() + 1
            var d = time.getDate()
            var h = time.getHours()
            var mm = time.getMinutes()
            var s = time.getSeconds()
            return "" + y + this.add0(m) + this.add0(d) + this.add0(h) + this.add0(mm) + this.add0(s)
        },
        add0(m) {
            return m < 10 ? "0" + m : m
        },
        searchList() {
            this.fetchData()
        },
        clearUpdata() {
            document.getElementById("fileName").value = ""
            this.dataForm.productUrl = undefined
            this.$forceUpdate()

            console.log(document.getElementById("fileName").files[0])
        },
        exportExcel() {
            requestByGet(api.excel, { num: 2 }, "blob").then(value => {
                util.downloadFile(value, "application/vnd.ms-excel", "" + ".xlsx")
                // if (value.code === 2000) {
                //     this.fetchData()
                // }
            })
        }
    }
}
</script>

<style lang="less" scoped>
.inputStyle {
    width: 200px;
}

.app-container {
    width: 96%;
    margin: auto;

    .top_search {
        div {
            &:nth-child(2) {
                margin: 0 20px;
            }
        }

        .search_input {
            width: 13vw;
            max-width: 400px;
            min-width: 100px;
        }
    }
}

.upload {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .uploadIcon {
        width: 30px;
        height: 30px;

        .icon {
            width: 100%;
            height: 100%;
            font-size: 10px;
        }
    }

    .clear {
        width: 60px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor: pointer;
    }
}
</style>
