<template>
    <div class="app-container">
        <!-- <el-button @click="addClick('')">
  <i class="el-icon-plus" />
  
</el-button>
<el-button @click="handleMultiDelete">
  <i class="el-icon-delete" />
  
</el-button> -->
        <div style="margin-top: 15px; width: 300px">
            <el-input placeholder="id" v-model="params.userId" class="input-with-select" @blur="searchuser">
                <el-button slot="append" icon="el-icon-search" @click="searchuser"></el-button>
            </el-input>
        </div>
        <el-table
            v-loading="dataLoading"
            style="margin-top: 10px"
            :data="dataList"
            element-loading-text="Loading"
            border
            @selection-change="handleSelectionChange"
        >
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.nickname }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.iphone }}</span>
                </template>
            </el-table-column>
            <el-table-column label="EMAIL" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.email }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.address }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.vip == "0" ? "" : "vip" }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="100">
                <template slot-scope="scope">
                    <el-button
                        type="warning"
                        size="mini"
                        icon="el-icon-edit"
                        circle
                        @click="updateClick(scope.row, '')"
                    />
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 30px; float: right">
            <el-pagination
                background
                :current-page="params.pageNum"
                :page-sizes="pageSizes"
                :page-size="params.pageSize"
                layout="total, prev, pager, next, sizes"
                :total="totalSize"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="70%"
                modal
                :close-on-click-modal="false"
                :append-to-body="true"
            >
                <el-form ref="oneForm" label-position="right" label-width="150px" :model="dataForm" :rules="rules">
                    <el-form-item label=":" class="avatar">
                        <el-avatar
                            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
                            :size="100"
                            v-if="dataForm.headimgurl == null"
                        ></el-avatar>
                        <el-avatar :src="dataForm.headimgurl" :size="100" v-else></el-avatar>
                    </el-form-item>
                    <el-form-item label="id:">
                        <span>{{ dataForm.userId }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.nickname }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.iphone }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.email }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.company }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.address }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{
                            dataForm.vip == "0"
                                ? ""
                                : dataForm.vip == "1"
                                ? ""
                                : dataForm.vip == "2"
                                ? ""
                                : ""
                        }}</span>
                    </el-form-item>
                    <el-form-item label=":">
                        <span>{{ dataForm.createTime }}</span>
                    </el-form-item>
                    <el-form-item label=":" prop="delivery">
                        <el-switch v-model="switchValue" active-color="red" @change="switchChange"></el-switch>
                    </el-form-item>
                </el-form>
                <div slot="footer" style="text-align: center" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"></el-button>
                    <!-- <el-button type="primary" @click="updateFlag == false ? handleAdd(dataForm) : handleUpdate(dataForm)"
  > </el-button
> -->
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { msgBox, customizeMsgBox } from "@/util/msgbox"
//   import { UTCToformat } from "@/utils";
import { requestByGet, requestByPost, requestByPut, requestByDelete } from "@/api/globalApi"
import api from "@/api/apis"

export default {
    name: "OrderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        return {
            dataLoading: false, // 
            dataList: [], // 
            multipleSelection: [], // 
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10, userId: "" }, //  1,10 ----  +
            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 
            dataForm: {}, // dialog 
            rules: {}, // 
            switchValue: false
        }
    },
    created() {
        this.fetchData()
    },
    methods: {
        // 
        fetchData() {
            this.dataLoading = true
            requestByGet(api.userList, this.params)
                .then(value => {
                    console.log(value)
                    this.dataLoading = false
                    if (value.code == 200) {
                        this.dataList = value.rspdata.records

                        this.totalSize = value.rspdata.total
                    }
                    if (value.code == 403) {
                        localStorage.removeItem("jwt")
                    }
                })
                .catch(error => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    formdata.orderId = this.superiorId
                    requestByPost(api.orderProduct, JSON.stringify(formdata)).then(v => {
                        if (v.code === 2000) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPut(api.orderProduct, JSON.stringify(formdata)).then(value => {
                        if (value.code === 2000) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: ""
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then(value => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        // 
        addClick(title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = false
            // 
            this.dataForm = {}
        },
        // 
        updateClick(val, title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = val
            if (val.interdiction == 1) {
                this.switchValue = true
            } else {
                this.switchValue = false
            }
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },
        switchChange(val) {
            val == true ? (this.dataForm.interdiction = 1) : (this.dataForm.interdiction = 0)

            let req = {
                code: this.dataForm.interdiction,
                userId: this.dataForm.userId
            }

            console.log(req)

            requestByPost(api.band, {}, req)
                .then(value => {
                    console.log(value)
                })
                .catch(error => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        searchuser() {
            this.fetchData()
        }
    }
}
</script>

<style lang="less" scoped>
.el-select .el-input {
    width: 130px;
}

.input-with-select .el-input-group__prepend {
    background-color: #fff;
}

.inputStyle {
    width: 200px;
}

.app-container {
    width: 96%;
    margin: auto;
}

.avatar {
    display: flex;
    justify-content: left;
    align-items: center;

    &::v-deep .el-form-item__content {
        margin-left: 0 !important;
    }
}
</style>
