<template>
    <div class="app-container">
        <div class="top_search">
            <el-select v-model="params.isEnable" placeholder="">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                ></el-option>
            </el-select>
            <el-input placeholder="" v-model="params.content" clearable class="search_input"></el-input>
            <el-button @click="searchList">
                <i class="el-icon-search" />
                
            </el-button>
            <!--            <el-button type="primary" >-->
            <!--                <i class="el-icon-plus" />-->
            <!--                -->
            <!--            </el-button>-->
            <el-button type="primary" @click="exportExcel">
                <i class="el-icon-download" />
                Excel
            </el-button>
        </div>
        <el-table
            v-loading="dataLoading"
            style="margin-top: 10px"
            :data="dataList"
            element-loading-text="Loading"
            border
            @selection-change="handleSelectionChange"
        >
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branArea }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branNumber }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branZhiBei }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branContact }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branContactIphone }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.createTime }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.branEstablishPeople }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span :style="scope.row.branStatue == 1 ? 'color:green' : 'color:red'">{{
                        scope.row.branStatue == 1 ? "" : ""
                    }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="150">
                <template slot-scope="scope">
                    <el-button type="text" @click="handleDelete(scope.$index, scope.row)"></el-button>
                    <el-button type="text" @click="updateClick(scope.row, '')"></el-button>
                    <el-button type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="margin-top: 30px; float: right">
            <el-pagination
                background
                :current-page="params.pageNum"
                :page-sizes="pageSizes"
                :page-size="params.pageSize"
                layout="total, prev, pager, next, sizes"
                :total="totalSize"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            />
        </div>
        <template v-if="dialogFormVisible">
            <el-dialog
                :title="dialogTitle"
                :visible.sync="dialogFormVisible"
                width="50%"
                modal
                :close-on-click-modal="true"
                :append-to-body="true"
                :modal-append-to-body="false"
            >
                <el-form ref="oneForm" label-position="right" label-width="150px" :model="dataForm" :rules="rules">
                    <el-form-item label=": " prop="area">
                        <el-cascader v-model="dataForm.area" size="large" :options="pcaTextArr"></el-cascader>
                    </el-form-item>

                    <el-form-item label=": " prop="branchName">
                        <el-input
                            v-model.lazy.trim="dataForm.branchName"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="branchNumber">
                        <el-input
                            v-model.lazy.trim="dataForm.branchNumber"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="lastName">
                        <el-select v-model.lazy.trim="dataForm.lastName" placeholder="">
                            <el-option
                                v-for="item in options2"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            >
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label=": " prop="branchZhi">
                        <el-input
                            v-model.lazy.trim="dataForm.branchZhi"
                            clearable
                            placeholder=""
                            style="width: 60%"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="enableNumber">
                        <el-input
                            v-model="dataForm.enableNumber"
                            clearable
                            placeholder=""
                            style="width: 60%"
                            type="number"
                        />
                    </el-form-item>
                    <el-form-item label=": " prop="enableNumber">
                        <el-switch v-model="dataForm.branStatue" active-text="" inactive-text=""></el-switch>
                    </el-form-item>
                </el-form>
                <div slot="footer" style="text-align: center" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button
                        type="primary"
                        @click="updateFlag === false ? handleAdd(dataForm) : handleUpdate(dataForm)"
                        > 
                    </el-button>
                </div>
            </el-dialog>
        </template>
    </div>
</template>
<script>
import { msgBox, customizeMsgBox } from "@/util/msgbox"
//   import { UTCToformat } from "@/utils";
import { requestByGet, requestByPost, requestByPut, requestByDelete } from "@/api/globalApi"
import api from "@/api/apis"
import { pcaTextArr } from "element-china-area-data"
import util from "@/util/index"
import { BranchOption } from "@/api/project"

export default {
    name: "OrderProduct",
    components: {},
    // eslint-disable-next-line vue/require-prop-types
    props: ["superiorId"],
    data() {
        var validatePass = (rule, value, callback) => {
            console.log(value)
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass2 = (rule, value, callback) => {
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass3 = (rule, value, callback) => {
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass4 = (rule, value, callback) => {
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        var validatePass5 = (rule, value, callback) => {
            if (value == "" || value == undefined) {
                callback(new Error(""))
            } else {
                callback()
            }
        }
        return {
            dataLoading: false, // 
            dataList: [], // 
            multipleSelection: [], // 
            totalSize: 0, // 
            pageSizes: [10, 30, 50], // 
            params: { pageNum: 1, pageSize: 10, isEnable: "", content: "" }, //  1,10 ----  +
            pcaTextArr,
            dialogFormVisible: false, // dialog 
            dialogTitle: "", // dialog
            updateFlag: false, // 
            dataForm: {
                area: [],
                branchName: "",
                branchNumber: "",
                lastName: "",
                branchZhi: "",
                enableNumber: 0,
                branStatue: false
            },

            rules: {
                area: [{ validator: validatePass, trigger: "blur" }],
                branchName: [{ validator: validatePass2, trigger: "blur" }],
                branchNumber: [{ validator: validatePass3, trigger: "blur" }],
                lastName: [{ validator: validatePass4, trigger: "blur" }],
                branchZhi: [{ validator: validatePass5, trigger: "blur" }]
            },
            fileList: [],
            options: BranchOption,

            options2: [
                {
                    value: "",
                    label: ""
                },
                {
                    value: "",
                    label: ""
                }
            ]
        }
    },
    created() {
        // console.log(this.superiorId)
        this.fetchData()
    },
    methods: {
        // 
        fetchData() {
            this.dataLoading = true
            requestByGet(api.branchList, this.params)
                .then(value => {
                    this.dataLoading = false

                    console.log(value)
                    if (value.rspdata !== "" && value.rspdata !== null) {
                        console.log(value.rspdata.records)

                        this.dataList = value.rspdata.records
                        this.totalSize = value.rspdata.total
                    } else {
                        this.dataList = []
                        this.totalSize = 0
                    }
                    if (value.code == 403) {
                        localStorage.removeItem("jwt")
                    }
                })
                .catch(error => {
                    msgBox(1000)
                    this.dataLoading = true
                })
        },
        // 
        handleSelectionChange(val) {
            this.dialogVisible = true
            this.multipleSelection = val
        },
        // 
        handleAdd(formdata) {
            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPost(api.Product, JSON.stringify(formdata)).then(value => {
                        if (value.code === 200) {
                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}
                        } else {
                            customizeMsgBox(4, "")
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleUpdate(formdata) {
            let reqData = {
                branId: formdata.branId,
                branArea: formdata.area.join(","),
                branName: formdata.branchName,
                branNumber: formdata.branchNumber,
                branStatue: formdata.branStatue === true ? 1 : 0,
                branZhiBei: formdata.branchZhi,
                enableNumber: formdata.enableNumber,
                lastName: formdata.lastName
            }

            this.$refs.oneForm.validate(valid => {
                if (valid) {
                    requestByPut(api.branch, JSON.stringify(reqData)).then(value => {
                        console.log(value)
                        if (value.code === 200 || value.code === 300) {
                            if (value.code === 300) customizeMsgBox(3, "")

                            this.dialogFormVisible = false
                            this.fetchData()
                            // 
                            this.dataForm = {}

                            customizeMsgBox(1, "")
                        }
                    })
                } else {
                    msgBox(300)
                }
            })
        },
        // 
        handleMultiDelete() {
            if (this.multipleSelection !== "" && this.multipleSelection != null) {
                var idArr = { c: [] }
                this.multipleSelection.forEach((item, index) => {
                    idArr.c.push(item.productId)
                })
                if (idArr.c.length > 0) {
                    this.$confirm("", "", {
                        confirmButtonText: "",
                        cancelButtonText: ""
                    }).then(() => {
                        requestByDelete(api.orderProduct, JSON.stringify(idArr)).then(value => {
                            if (value.code === 2000) {
                                this.fetchData()
                            }
                        })
                    })
                }
            }
        },
        handleDelete(index, row) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    console.log(index, row)

                    let idArr = { ids: [] }

                    idArr.ids.push(row.branId)

                    requestByDelete(api.branch, JSON.stringify(idArr)).then(value => {
                        if (value.code === 200) {
                            customizeMsgBox(1, "")
                            this.fetchData()
                        }
                    })
                })
                .catch(() => {
                    customizeMsgBox(4, "")
                })
        },
        // 
        addClick(title) {
            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = false
            // 
            this.dataForm = {}
        },
        // 
        updateClick(val, title) {
            let data = {
                branId: val.branId,
                area: val.branArea.split(","),
                branchName: val.branName,
                branchNumber: val.branNumber,
                lastName: val.lastName,
                branchZhi: val.branZhiBei,
                enableNumber: val.enableNumber,
                branStatue: val.branStatue !== 0
            }

            this.dialogTitle = title
            this.dialogFormVisible = true
            this.updateFlag = true
            this.dataForm = data

            this.$forceUpdate
        },
        // /
        handleSizeChange(val) {
            this.params.pageSize = val
            this.fetchData()
        },
        // 
        handleCurrentChange(val) {
            this.params.pageNum = val
            this.fetchData()
        },
        timestamp() {
            var time = new Date()
            var y = time.getFullYear()
            var m = time.getMonth() + 1
            var d = time.getDate()
            var h = time.getHours()
            var mm = time.getMinutes()
            var s = time.getSeconds()
            return "" + y + this.add0(m) + this.add0(d) + this.add0(h) + this.add0(mm) + this.add0(s)
        },
        add0(m) {
            return m < 10 ? "0" + m : m
        },
        searchList() {
            this.fetchData()
        },
        clearUpdata() {
            document.getElementById("fileName").value = ""
            this.dataForm.productUrl = undefined
            this.$forceUpdate()

            console.log(document.getElementById("fileName").files[0])
        },
        exportExcel() {
            requestByGet(api.excel, { num: 1 }, "blob").then(value => {
                util.downloadFile(value, "application/vnd.ms-excel", "" + ".xlsx")
                // if (value.code === 2000) {
                //     this.fetchData()
                // }
            })
        }
    }
}
</script>

<style lang="less" scoped>
.inputStyle {
    width: 200px;
}

.app-container {
    width: 96%;
    margin: auto;

    .top_search {
        div {
            &:nth-child(2) {
                margin: 0 20px;
            }
        }

        .search_input {
            width: 13vw;
            max-width: 400px;
            min-width: 100px;
        }
    }
}

.upload {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .uploadIcon {
        width: 30px;
        height: 30px;

        .icon {
            width: 100%;
            height: 100%;
            font-size: 10px;
        }
    }

    .clear {
        width: 60px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor: pointer;
    }
}
</style>
